import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import {ThemeConfig} from 'bootstrap-darkmode';
import {useState} from 'react';
import { Outlet } from "react-router-dom";

function WebsiteNavBar() {
  const [currentTheme, setCurrentTheme] = useState("light");



  const themeConfig = new ThemeConfig();
  themeConfig.displayTheme(currentTheme);

  return (
    <>
    <Navbar expand="lg">
      <Container>
        <Navbar.Brand href="home">Abdel's Personal Website</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link href="home">Home</Nav.Link>
            <Nav.Link href="notes">Notes</Nav.Link>
            <Nav.Link href="flashcards">Flashcards</Nav.Link>
            {/* <NavDropdown title="Dropdown" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">
                Another action
              </NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">
                Separated link
              </NavDropdown.Item>
            </NavDropdown> */}
          </Nav>
          <Nav>
            <Nav.Link onClick={() => {currentTheme === "light" ? setCurrentTheme("dark") : setCurrentTheme("light")}}>
              {currentTheme.charAt(0).toUpperCase() + currentTheme.slice(1)} Mode
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
    <Outlet />
    </>
  );
}

export default WebsiteNavBar;

