// Cloud Storage...
const storage = getStorage(app);

const dataFormatPath = "gs://personalgitwebsite.appspot.com/Japanese/dataFormat";

const gsReferenceDF = ref(storage, dataFormatPath);

export function uploadData(){
    const dataFormat = "index;example-sentence;kanji;reading;kana-reading;meaning;notes"
    uploadString(gsReferenceDF, dataFormat).then(snapshot => {
        console.log(snapshot);
        console.log("Uploaded raw String");
    }).catch(err => {
        console.log(err)
    });
}

export function readData(){
    return getDownloadURL(gsReferenceDF);
}