import Button from 'react-bootstrap/Button';
import {uploadData, readData} from './FirebaseAPI';
import {useState} from 'react';


const Flashcards = () => {

    const [fileFormat, setFileFormat] = useState("");

    const callUploadData = () => {
        uploadData();
        return;
    }

    const loadData = async () => {
        readData().then(url => {
            const xhr = new XMLHttpRequest();
            xhr.responseType = 'blob';
            xhr.onload = async (event) => {
            const blob = xhr.response;
            const stff = await blob.text();
                console.log(stff)
                setFileFormat(stff);
            };
            xhr.open('GET', url);
            xhr.send();
        }).catch(err => {
            console.log(err);
        })
        return;
    }

    return (<div>
        TODO:

        1: Flash card UI
        2: Firebase set up 
        3: Card Contents....
        <br />
        <Button variant="primary" onClick={callUploadData}> Upload Data </Button>
        <Button onClick={loadData}> Load Data </Button>
        <br />
        Data in fileFormat: {fileFormat}
    </div>)
}

export default Flashcards;